.wrapper{height:max(229px,21vw);max-height:323px;width:max(325px,30vw);max-width:455px}.card{background-color:#fff;width:100%;height:85%;min-height:230px;border-radius:25px;padding:max(0.9375rem,1.5vw);display:flex;flex-direction:column;justify-content:center}.contacts{height:150px;display:flex;align-items:center;gap:15px;overflow-x:scroll;margin-bottom:20px}.input{width:100%;display:flex;align-items:center;justify-content:space-between}.input>p{white-space:nowrap;margin:0;margin-right:10px;font-size:clamp(0.75rem,0.661971831rem + 0.3755868545vw,1rem);color:var(--text-color)}.input>form{display:flex;align-items:center;background-color:var(--background);border-radius:50px;height:50px}.input>form>input{width:65%;background-color:rgba(0,0,0,0);border:none;outline:none;color:var(--text-color);margin-left:15px}.input>form>input::placeholder{color:var(--text-color)}.input>form>button{background-color:var(--primery);color:#fff;border:none;border-radius:50px;width:125px;height:100%;display:flex;align-items:center;justify-content:center}.input>form>button>p{margin:0;margin-right:10px;text-align:center;font-weight:500;font-size:clamp(0.75rem,0.661971831rem + 0.3755868545vw,1rem)}.disabled{background-color:var(--text-color) !important}.isError::placeholder{color:var(--red) !important}@media(width <= 768px){.wrapper{width:max(325px,100%);max-width:100%;margin-bottom:35px}}